import React, { Component, Suspense, lazy } from 'react';
import { Route, Switch } from 'react-router-dom';
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';

import { UserLayout, BlackBlock } from 'components';

import './index.less';

const Notes = lazy(() => import('./Notes'));
const Study = lazy(() => import('./Study'));
const Setting = lazy(() => import('./Setting'));

class UserIndex extends Component {
  render() {
    return (
      <UserLayout>
        <Suspense
          fallback={
            <Spin indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
              <BlackBlock />
            </Spin>
          }>
          <Switch>
            <Route exact path="/user" component={Notes} />
            <Route exact path="/user/study" component={Study} />
            <Route exact path="/user/setting" component={Setting} />
          </Switch>
        </Suspense>
      </UserLayout>
    );
  }
}

export default UserIndex;
